@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Rock+Salt&family=Ruthie&display=swap);
:root {
  --primary-AliceBlue: #F0F8FF;
  --primary-linen: #FAF0E6;
  --primary-white: #ffffff;
  --primary-white-smoke: #F5F5F5;
  --primary-grey: #D3D3D3;
  --primary-light-grey: #DCDCDC;
  --primary-dark-grey: #A9A9A9;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(52, 58, 64);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-black: #000000;
  --primary-light-purple: #d9d9d9;
}

body {
  font-family: 'Roboto Condensed',
    sans-serif;
}

/* ----------  NAVBAR ---------- */

.navbar-brand h1 {
  font-family: 'Ruthie',
    cursive;
  color: #ffffff;
  color: var(--primary-white);
  letter-spacing: 0.5rem;
}

nav {
  z-index: 2;
}

/* ----------  LINK ---------- */

nav a.nav-link {
  font-size: .8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav li a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
  /* adding border bottom when click the navbar nav li wont jump */
  border-bottom: 0.1875rem solid rgba(52, 58, 64);
  border-bottom: 0.1875rem solid var(--primary-dark);
}

@media(max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

.navbar-nav li a.active,
.navbar-nav li a.active:focus {
  border-bottom: 0.1875rem solid #f9ab00;
  border-bottom: 0.1875rem solid var(--primary-orange);
}

.navbar-nav li a:hover {
  border-bottom: 0.1875rem solid #f9ab00;
  border-bottom: 0.1875rem solid var(--primary-orange);
}

/* ----------  HEADER ---------- */

.header-wrapper {
  background-color: rgba(52, 58, 64);
  background-color: var(--primary-dark);
  position: relative;
  /* background: url(backg.png) no-repeat;
  background-size: cover;
  background-position: center; */
  height: 100vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1 {
  color: #ffffff;
  color: var(--primary-white);
  /* text-transform: uppercase; */
  font-family: 'Architects Daughter',
    cursive;
  color: #ffffff;
  color: var(--primary-white);
  letter-spacing: 0.5rem;
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }

  .typed-text {
    font-size: 1.3rem !important;
  }
}

/* ----------  TYPED TEXT ---------- */

.typed-text {
  font-family: 'Roboto Condensed',
    sans-serif;
  font-size: 2rem;
  font-weight: bolder;
  color: #ef4035;
  color: var(--primary-red);
  letter-spacing: 0.5rem;
}

/* ----------  HEADER BTN ---------- */

.btn-main-offer {
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #ffffff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #ffffff;
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

/* ----------  PARTICLE ---------- */

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;

}

@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/* ----------  ABOUT ME ---------- */

#about {
  height: 110vh;
  width: 100vw;

}

.photo-wrap {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}

.about-heading {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 2rem;
}

.about-heading .row {
  padding-left: 0;
}

@media(max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}

/* ----------  EXPERIENCE ---------- */

.experience {
  padding: 0.7rem;
  background-color: rgba(52, 58, 64);
  background-color: var(--primary-dark);
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
}

/* line */
.experience-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ffffff;
  background: var(--primary-white);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid #ffffff;
  border: 0.3125rem solid var(--primary-white);
  background: #f9ab00;
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;

}

.timeline-content {
  width: 95%;
  padding: 0 0.09375rem;
  color: #A9A9A9;
  color: var(--primary-dark-grey);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
}

.hr {
  background-color: #ffffff;
  background-color: var(--primary-white);
}

/* ----------  PORTFOLIO ---------- */

.portfolio h1 {
  margin-top: 2rem;
  text-align: center;
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
}

.header-port {
  min-height: 80px;
  cursor: pointer;
}

.header-port .logo-port {
  font-size: 3em;
  text-transform: uppercase;
  font-weight: 1200;
  line-height: 80px;
  /* float: left; */
  margin-left: 50px;

}

.header-port ul {
  float: right;
}

.header-port ul li {
  list-style: none;
  display: inline-block;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 900;
  padding: 0 20px;
  line-height: 80px;
  margin-right: 30px;

}

.header-port ul li:hover,
.header-port ul .active {
  background: rgb(195, 136, 230);
}

.port {
  width: 100%;
  padding: 10px;
  margin-top: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.thumb img {
  width: 320px;
  height: 280px;
  display: block;
  object-position: top;
  object-fit: cover;
  -webkit-align-items: center;
          align-items: center;
}


.thumb {
  width: 24%;
  min-width: 350px;
  height: 400px;
  text-align: center;
  cursor: pointer;
  padding: 10px;
  margin: 1%;

}


.thumb h5 {
  margin-top: 0px;
  text-align: center;
  padding-top: 4%;
}

.portfolio {
  padding: 0.7rem;
}


/* ----------  CONTACT ---------- */

.contact {
  background-color: rgba(52, 58, 64);
  background-color: var(--primary-dark);
  text-align: center;
}

.contact h1 {

  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
}


.containerLogin {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  max-width: 1200px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  z-index: 1;
}

button {
  padding: 5px;
}

.contentCard img {
  width: 150px;
  height: 150px;

}

.contentCard button {
  margin-top: 10%;
}


.containerLogin .cardS {
  position: relative;
  width: 280px;
  height: 400px;
  margin: 30px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
}

.containerLogin .cardS .contentCard {
  padding: 20px;
  text-align: center;

}


/* ----------  MEDIA ---------- */

@media(max-width:768px) {
  .experience .container {
    padding-left: 0;
  }

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }

  .timeline-block-right {
    float: none;
    width: 100%;

  }

  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}



/* ----------  RESUME ---------- */

.resume h1 {
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
  text-align: center;

}


.resume {
  padding: 0.7rem;
  height: 100vh;
  width: 100vw;
}

.pdf {
  background-color: skyblue;
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: stretch;
          justify-content: stretch;
}

/* ----------  OVERFLOW BOX ---------- */

.overflow {
  position: absolute;
  opacity: 0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top: 0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #f9ab00;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.popupbox-wrapper {
  margin-top: 7rem;
}

/* ----------  OVERFLOW BOX ---------- */

.portfolio-image-popupbox {
  width: 45rem;
  /* height: 40rem; */
  padding: 0 0.5rem;
}

.hyper-link {
  cursor: pointer;
  color: #f9ab00;
  color: var(--primary-orange);

}

.hyper-link:hover {
  color: #ef4035;
  color: var(--primary-red);

}

/* ----------  MEDIa ---------- */


@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }

  .pdf {
    width: 100%;
  }
}


@media(max-height: 570px) {
  .popupbox-wrapper {
    height: 100%;
  }

  .pdf {
    height: 100%;
  }

  .portfolio-image-popupbox {
    width: 50%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%;
  }

  .pdf {
    height: 100%;
  }

  .portfolio-image-popupbox {
    width: 30%;
  }
}

/* ----------  FOOTER ---------- */

.footer {
  background: rgba(52, 58, 64);
  background: var(--primary-dark);
  color: #D3D3D3;
  color: var(--primary-grey);
  /* padding: 1rem; */
  /* padding-bottom: 1rem; */
}


.footer .circle {
  position: relative;
  /* margin: 0.10rem; */
  margin-right: 2%;
  background: #A9A9A9;
  background: var(--primary-dark-grey);
  border-radius: 20%;
  width: 2rem;
  height: 2rem;
}

.footer .icon {
  font-size: 1.2rem;
  position: absolute;
  color: rgba(52, 58, 64);
  color: var(--primary-dark);
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.footer .icon:hover {
  color: rgba(52, 58, 64);
  color: var(--primary-dark);
  cursor: pointer;
}

.footer .circle:hover {
  background: #DCDCDC;
  background: var(--primary-light-grey);
}
